.main-navigation {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-100%);
  background-color: $black;
  transition: transform $transition-speed $transition-easing;
  z-index: 99;

  .container {
    height: 100%;

    .main-menu {
      height: calc(100% - 290px);
      align-items: center;
      padding: 130px 0 160px 0;

      @media #{$break-sm-up} {
        height: calc(100% - 260px);
        padding: 130px 0;
      }
  
      ul {
        margin: 0;
        padding: 0;
        list-style-type: none;
        
        li {
          overflow: hidden;
          
          a {
            font-weight: $font-weight-black;
            font-size: $font-size-h1-mobile;
            color: $white;
            line-height: 1;
            text-transform: uppercase;
            text-decoration: none;
            transition: color $transition-speed $transition-easing;

            @media #{$break-sm-square-up} {
              font-size: $font-size-h1;
            }

            &:hover {
              color: $blue;
            }
          }

          &.current-menu-item {
            a {
              color: $blue;
            }
          }
        }
      }
    }
  }
}

@media #{$break-sm-square-down} {
  .mobile.landscape {
    .main-navigation {
      .main-menu {
        height: calc(100% - 260px);
        padding: 130px 0;
  
        .menu-main-menu-container {
          width: 100%;
  
          ul {
            display: flex;
            flex-wrap: wrap;
            margin: -15px;
    
            li {
              margin: 15px;
  
              a {
                font-size: 41px;
              }
            }
          }
        }
      }
    }
  }
}